<script lang='ts'>
	import LandingSection from './sections/LandingSection.svelte'
	import AboutMeSection from './sections/AboutMeSection.svelte'
	import ProjectsSection from './sections/ProjectsSection.svelte'
	import SkillsSection from './sections/SkillsSection.svelte'
	import FooterSection from './sections/FooterSection.svelte'
	import {GlobalStore} from './global_store'
	import EaseScrolling from './utils/ease_scrolling'
	import {cubicInOut} from 'svelte/easing'
	import {MetaTags} from 'svelte-meta-tags'
	import {isLoading, _} from 'svelte-i18n'
	import {i18n, LocaleList, LocaleFullName, isInvalidLocale, Locale} from './i18n'
	import {modalBgAnim, socialModalAnim} from './utils/transitions'
	import {vibrate} from './utils/misc'

	const pageScroll = {y: 0, x: 0}
	function scrollingApp(e): void {
		pageScroll.y = e.target.scrollTop
		pageScroll.x = e.target.scrollLeft
	}

	let MainEl = null
	async function goToSection(sectionID) {
		if (MainEl === null) return
		await EaseScrolling(MainEl, {
			top: document.getElementById(sectionID.detail).offsetTop,
			duration: 1000,
			easing: cubicInOut,
		})
		// Remvoed due to scroll bug in iOS iPhone Safari
		// window.location.hash = sectionID.detail
	}

	let selectingLang = false
	function closeLangSelect(): void {
		selectingLang = false
	}
	function openLangSelect(): void {
		selectingLang = true
	}
	function toggleLangSelect(): void {
		vibrate()
		if (selectingLang) closeLangSelect()
		else openLangSelect()
	}
	function selectLang(locale: Locale): void {
		vibrate()
		i18n.switch(locale)
		closeLangSelect()
	}

	let socialModalContainer: HTMLElement
	$:GlobalStore.setSocialModalEl(socialModalContainer)
	$:selectedSocial = (
		$GlobalStore.openedSocialModal !== null &&
		$GlobalStore.socialMedia[$GlobalStore.openedSocialModal]
	)

	function closeSocialModal() {
		GlobalStore.closeSocialModal()
	}
</script>



